
function ErrorDetail(props) {
    const { id, url, message, type, eventKey, lineno, colno, stack } = props.data || {};

    return (
        <div className={'error-detial'}>
             <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>错误ID</div>
                <div>{id}</div>
            </div>
            <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>错误栈</div>
                <div dangerouslySetInnerHTML={{__html:stack}}></div>
            </div>
            <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>文件地址</div>
                <a href={url} target={'_blank'}>{url}</a>
            </div>
            <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>源文件报错行</div>
                <div>{colno}</div>
            </div>
            <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>源文件报错列</div>
                <div>{lineno}</div>
            </div>
            <div className={'error-detial-item'}>
                <div className={'error-detial-item-title'}>源文件代码</div>
                <div className={'error-code'}><span>{colno}</span>console.log(a)</div>
            </div>
        </div>
    )
}

export default ErrorDetail