import { Table, Space, Tag, Input, Form, Button, Modal } from 'antd'
import moment from 'moment';
import { useState } from 'react';
import ErrorDetail from './ErrorDetail'

const { Search } = Input;
const { Column } = Table;

function ErrorTable(props) {

    const [form] = Form.useForm();

    const [visible, setVisible] = useState();

    const [detial, setDetial] = useState(null)

    const data = [
        {
            id: "1637914017860@tDdWe5NC",
            url: 'http://localhost:8080/public/bundle/js/index.js',
            message: 'Uncaught ReferenceError: a is not defined',
            type: 'ReferenceError',
            time: moment(1637914017900).format('Y-MM-DD HH:mm'),
            events: 10,
            users: 20,
            eventKey: "385550366100A",
            status: 0,
            lineno: 10,
            colno: 12,
            stack: `ReferenceError: a is not defined 
            </br> at Module.<anonymous> (http://localhost:8080/public/bundle/js/index.js:91773:13)
            </br> at Module../src/index.js (http://localhost:8080/public/bundle/js/index.js:91777:30)
            </br> at __webpack_require__ (http://localhost:8080/public/bundle/js/index.js:833:30)
            </br> at fn (http://localhost:8080/public/bundle/js/index.js:130:20)
            </br> at Object.0 (http://localhost:8080/public/bundle/js/index.js:100459:18)
            </br> at __webpack_require__ (http://localhost:8080/public/bundle/js/index.js:833:30)
            </br> at http://localhost:8080/public/bundle/js/index.js:971:37
            </br> at http://localhost:8080/public/bundle/js/index.js:974:10`
        }
    ]


    const pagination = {
        pageSize: 10
    }

    const onCancel = () => {
        setVisible(false)
    }

    const openDetial = (record) => {
        setDetial(record);
        setVisible(true)
    }

    const onCreateError=()=>{
        console.log(a)
    }

    return (
        <div className={'error-table'}>
            <Form
                className={'error-search'}
                layout={'inline'}
                form={form}
            >
                <Form.Item label="错误文件" name={'url'}>
                    <Input placeholder="请输入错误文件" />
                </Form.Item>
                <Form.Item label="错误信息" name={'message'}>
                    <Input placeholder="请输入错误信息" />
                </Form.Item>
                <Form.Item label="事件key" name={'message'}>
                    <Input placeholder="请输入事件key" />
                </Form.Item>
                <Form.Item>
                    <Button type="primary">Submit</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" onClick={onCreateError}>createError</Button>
                </Form.Item>
            </Form>
            <Table
                rowKey={record => record.id}
                dataSource={data}
                pagination={pagination}
            >
                <Column title="错误文件" dataIndex="url" className={'error-text'} />
                <Column title="错误信息" dataIndex="message" className={'error-text'} />
                <Column title="错误类型" dataIndex="type" />
                <Column title="发生时间" dataIndex="time" />
                <Column title="事件数" dataIndex="events" />
                <Column title="用户数" dataIndex="users" />
                <Column title="事件key" dataIndex="eventKey" />
                <Column
                    title="状态"
                    dataIndex="status"
                    render={() => (
                        <Tag color="#f50">未处理</Tag>
                    )}
                />
                <Column
                    title="操作"
                    dataIndex="action"
                    render={(_, record) => (
                        <Space size="middle">
                            <a onClick={() => openDetial(record)}>查看</a>
                            <a>处理</a>
                            <a>解决</a>
                        </Space>
                    )}
                />

            </Table>
            <Modal
                width={800}
                title="错误详情"
                forceRender={true}
                visible={visible}
                onCancel={onCancel}
                footer={null}
            >
                <ErrorDetail data={detial} />
            </Modal>
        </div>
    )
}

export default ErrorTable