import { useEffect } from 'react'
import ErrorTable from './ErrorTable.js'
import {getErrorList} from 'services/service'

import './index.styl'

function ErrorMonitoring() {

    useEffect(()=>{

        getErrorList().then((res)=>{
            console.log('res',res)
        })

    },[])

    return (
        <div className={'error-monitoring'}>
            <ErrorTable data={[]} />
        </div>
    )

}

export default ErrorMonitoring